import React, { Component } from 'react';
import Modal from '../../ui-kit/modal/modal.component';
import Icon from '../../ui-kit/icon/icon.component';
import Button from '../../ui-kit/button/button.component';
import iconLogo from '../../../resources/svg/icon-logo.svg';
import iconToggle from '../../../resources/svg/icon-shape.svg';
import iconUK from '../../../resources/svg/icon-uk.svg';
import css from './header.component.scss';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loginModalIsOpen: false,
    };
    // this.openLoginModal = this.openLoginModal.bind(this);
  }
  render() {
    const { loginModalIsOpen } = this.state;
    // console.log(loginModalIsOpen);
    return (
      <header className={css.header}>
        <Icon name={iconLogo} className={css.logo} />
        <Button className={css.toggle}>
          <Icon name={iconToggle} className={css.iconTogle} />
        </Button>
        <div className={css.profile}>
          <Button className={css.button}>
            List your Vehicle
          </Button>
          <Button
            className={css.button}
            onClick={this.openLoginModal}
          >
            Log In
          </Button>
          <Button className={css.button}>
            Register for free
          </Button>
          <Icon name={iconUK} className={css.icon} />
        </div>
        <Modal
          appElement={document.querySelector('#root')}
          className={{
              base: 'myClass',
              afterOpen: 'myClass_after-open',
              beforeClose: 'myClass_before-close',
          }}
          overlayClassName={{
              base: 'myOverlayClass',
              afterOpen: 'myOverlayClass_after-open',
              beforeClose: 'myOverlayClass_before-close',
          }}
          isOpen={loginModalIsOpen}
          // onRequestClose={closeModal}
        >
          <span>Modal Open</span>
        </Modal>
      </header>
    );
  }
  openLoginModal = () => {
    this.setState({
      loginModalIsOpen: true,
    });
  }
}

export default Header;


